<template>
    <div id="pass-my">
        <img src="~assets/jsqbProject/app/imgs/bg.png"/>
        <div class="con">
            <div class="title">
                <p>借款金额</p>
                <p>
                    <h2>{{rangeValue+'元'}}</h2>
                </p>

            </div>
            <div class="range">
                <mt-range
                  v-model="rangeValue"
                  :min="2000"
                  :max="5000"
                  :step="100"
                  :bar-height="10">
                </mt-range>
                <p>
                    <span class="fl">2000</span>
                    <span class="fr">5000</span>
                </p>
            </div>
            <p class="pass-p1 desp">
                <span class="fl">借款期限</span><span class="fr">30天</span>
            </p>
            <p class="pass-p2 desp" style="margin-top:0">
                <span class="fl">借款息费</span><span class="fr">{{(rangeValue*0.028).toFixed(2)+'元'}}</span>
            </p>
            <p class="btn-pass" @click="tipShowfn">
                立即申请
            </p>
            <p class="my-txt">
                *息费包含借款所需的利息和服务费
            </p>
        </div>
        <div class="tips" v-if="tipShow">
            <div class="tip-con">
                <img class="fr" src="~assets/jsqbProject/app/imgs/closed.png" @click="nonetipShow" />
                <p>
                    您尚有一笔订单未还款，现在还款即可获得包过钱包借款资格，大额低息借的到！
                </p>
                <p class="btn-pass1" @click="jumpto">
                    立即还款
                </p>
            </div>
        </div>
    </div>
</template>
<script>
import { Range ,Button } from 'mint-ui';
import Vue from 'vue'
Vue.component(Range.name, Range);
Vue.component(Button.name, Button);

    export default {
        name:'pass',
        data () {
            return {
                rangeValue:3000,
                tipShow:''
            }
        },
        methods : {
            tipShowfn() {
                this.tipShow = '1'
            },
            nonetipShow () {
                this.tipShow = ''
            },
            jumpto() {
                return nativeMethod.returnNativeMethod('{"skip_code":"'+102+'"}');
            }

        }
    }
</script>
<style lang="less">
    @import './less.less';
</style>



